
import { defineComponent } from 'vue'
import Breadcrumb from '@/base-ui/breadcrumb'
import UserInfo from './user-info.vue'

export default defineComponent({
  components: {
    UserInfo,
    Breadcrumb
  },
  props: {
    collapse: Boolean,
    breadcrumbs: Array
  },
  emits: ['foldChange'],
  setup(props, { emit }) {
    const handleFoldClick = () => {
      emit('foldChange', !props.collapse)
    }

    return {
      handleFoldClick
    }
  }
})
