
import { useRouter } from 'vue-router'
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    collapse: Boolean,
    defaultValue: String,
    userMenus: Array
  },
  setup() {
    const router = useRouter()

    const handleMenuItemClick = (item: any) => {
      if (item) {
        router.push(item.url)
      }
    }

    return {
      handleMenuItemClick
    }
  }
})
